.main-img{
  position: relative;
  width: 30rem;
  height: 18rem;
}

.img-name{
  position: absolute;
  top: 15px;
  right: 25px;
  padding: 5px 20px;
  border-radius: 25px;
  background-color: rgb(1, 165, 219);
}

.main-img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.img-cards{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  margin-top: 30px;
  gap: 15px;
}

.img-card img{
  width: 100%;
  height: 180px;
  border-radius: 10px;
  cursor: pointer;
  object-fit: cover;
}

.clicked{
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.425);
  border: 1px solid  rgba(255, 255, 255, 0.637);
}

.main-img .icon{
  position: absolute;
  top: 50%;
  background-color: darkcyan;
  border-radius: 50%;
  font-size: 25px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.right-icon{
  right:-8px;
}

.left-icon{
  left:-8px;
}