*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  font-family: sans-serif;
}

html::-webkit-scrollbar{
  width: 8px;
  background-color: transparent;
}

html::-webkit-scrollbar-thumb{
  background-color: rgb(4, 187, 4);
}

::selection{
  background-color: rgb(4, 187, 4);
  color: aliceblue;
}

body {
  background-color: #111222;
  color: aliceblue;
}

.container{
  max-width: 1150px;
  margin: auto;
  padding:0 10px;
}